<template>
  <div>
    <h1 class="centralizado">Cadastro</h1>
    <h2 class="centralizado">{{ foto.titulo }}</h2>

    <h2 v-if="foto._id" class="centralizado">Alterando</h2>
    <h2 v-else class="centralizado">Incluindo</h2>
    <!-- @submit padro recarrega a pgina, redireciona -->
    <!-- .prevent  igual o prevent default do javascript -->
    <form @submit.prevent="grava()">
      <div class="controle">
        <label for="titulo">TTULO</label>
        <!-- .lazy s atualiza o model quando sair do campo -->
        <input data-vv-as="ttulo" name="titulo" v-validate data-vv-rules="required|min:3|max:30" id="titulo" autocomplete="off" v-model.lazy="foto.titulo" />
        <!-- VeeValidate que cria esse errors, a partir do name das tags -->
        <span class="erro" v-show="errors.has('titulo')">{{ errors.first('titulo') }}</span>
      </div>

      <div class="controle">
        <label for="url">URL</label>
        <input data-vv-as="URL" name="url" v-validate data-vv-rules="required" id="url" autocomplete="off" v-model.lazy="foto.url" />
        <span class="erro" v-show="errors.has('url')">{{ errors.first('url') }}</span>
        <imagem-responsiva v-show="foto.url" :url="foto.url" :titulo="foto.titulo" />
      </div>

      <div class="controle">
        <label for="descricao">DESCRIO</label>
        <textarea id="descricao" autocomplete="off" v-model="foto.descricao"></textarea>
      </div>

      <div class="centralizado">
        <meu-botao rotulo="GRAVAR" tipo="submit"/>
        <router-link :to="{ name: 'home' }"><meu-botao rotulo="VOLTAR" tipo="button"/></router-link>
      </div>

    </form>
  </div>
</template>

<script>
import ImagemResponsiva from '../shared/imagem-responsiva/ImagemResponsiva.vue'
import Botao from '../shared/botao/Botao.vue';
import Foto from '../../domain/foto/Foto';
import FotoService from '../../domain/foto/FotoService';

export default {

    components: {
        'imagem-responsiva': ImagemResponsiva, 
        'meu-botao': Botao
    },

    data() {
        return {
            foto: new Foto(),
            id: this.$route.params.id
        };
    },

    methods: {
        grava() {
            // existe no VeeValidate
            this.$validator.validateAll().then(success => {
                if (success) {
                    // this.$http.post('v1/fotos', this.foto)
                    this.service.cadastra(this.foto)
                        .then(() => {
                            if (this.id) this.$router.push({ name: 'home' });
                            this.foto = new Foto();
                        }, err => console.log(err));
                }
            });
        }
    },
    created() {
        this.service = new FotoService(this.$resource);

        if (this.id) {
            this.service.busca(this.id).then(foto => this.foto = foto);
        }
    }


}
</script>

<style scoped>
  .centralizado {
    text-align: center;
  }
  .controle {
    font-size: 1.2em;
    margin-bottom: 20px;

  }
  .controle label {
    display: block;
    font-weight: bold;
  }

 .controle label + input, .controle textarea {
    width: 100%;
    font-size: inherit;
    border-radius: 5px
  }

  .centralizado {
    text-align: center;
  }

  .erro {
    color: red;
  }
</style>



